<template>
  <div class="dynamic">
    <Publish class="publish-com" @needRefresh="getDynamicList" />
    <AllDynamic class="all-com" @needRefresh="getDynamicList" />
    <DynamicList
      class="list-com"
      v-for="(item, index) in dynamicList"
      :key="item.listID"
      :listInfo="item"
      v-if="dynamicList.length"
    />
  </div>
</template>

<script>
import AllDynamic from "@/components/dynamic/AllDynamic";
import Publish from "@/components/dynamic/Publish";
import DynamicList from "@/components/dynamic/DynamicList";
export default {
  components: {
    Publish,
    AllDynamic,
    DynamicList
  },
  created() {
    this.getDynamicList();
  },
  data() {
    return {
      dynamicList: []
    };
  },
  methods: {
    getDynamicList() {
      this.$api
        .getAllDynamicList({
          page: 0,
          pageSize: 10
        })
        .then(res => {
          console.log(res, "lllllll");
          this.dynamicList = res.data.data;
        });
    }
  }
};
</script>

<style scoped lang="scss">
.dynamic {
  .publish-com {
    margin-bottom: 0.3rem;
  }
  .list-com {
    margin: 0.3rem 0;
  }
}
</style>


